import React from 'react';
import Images from '../../../../constants/images';
import './styles.scss';
import { useHistory } from 'react-router-dom';

function HomeAboutUs() {
    const history = useHistory()
    const onList = () => {
        history.push(`/danh-sach`);
    }
    return (
        <section className="aboutus-section spad">
            <div className="container">
                <div className="row">
                    <div className="col-lg-6">
                        <div className="about-text">
                            <div className="section-title">
                                <span>V Chng Ti</span>
                                <h2>Luxury Homestay VN <br />Travel Homestay</h2>
                            </div>
                            <p className="f-para">Homestay Travel l mt nn tng kt ni gia ch nh v ngi thu nh (Home-Sharing).
                                Thng qua Homestay Travel du khch c nhu cu thu phng tr s lin h vi nhng ch nh ti a phng.</p>
                            <p className="s-para">Cc ch nh thng qua cc cn h ca mnh c th cho thu ngn hn kim tin mt cch d dng. Cn du khch s
                                c tri nghim dch v cn h thoi mi v thun tin nht.</p>
                            <a onClick={() => onList()} href className="primary-btn about-btn">t Phng Ngay</a>
                        </div>
                    </div>
                    <div className="col-lg-6">
                        <div className="about-pic">
                            <div className="row">
                                <div className="col-sm-6">
                                    <img src={Images.ABOUT_1} alt="" />
                                </div>
                                <div className="col-sm-6">
                                    <img src={Images.ABOUT_2} alt="" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default HomeAboutUs;