import 'antd/dist/antd.css';
import React from 'react';
import Images from '../../../../constants/images';
import './styles.scss';


function Banking(props) {
    return (
        <div className="method-banking-wrapper">
            <h5 className="text-center">THNG TIN THANH TON CHUYN KHON NGN HNG</h5>
            <div className="row content">
                <div className="col-12 col-lg-4">
                    <div className="method-payment">
                        <img src={Images.INTERNET_BANKING} alt="" />
                    </div>
                </div>
                <div className="col-12 col-lg-4 mr-10">
                    <div className="method-form">
                        <p className="title">
                            Thng tin ngi gi
                        </p>
                        <div className="form-item">
                            <span className="form-item__title">
                                H v tn
                            </span>
                            <span>
                                &nbsp;: &nbsp;
                            </span>
                            <span className="form-item">
                                {props.payment.fullname}
                            </span>
                        </div>
                        <div className="form-item">
                            <span className="form-item__title">
                                S in thoi
                            </span>
                            <span>
                                &nbsp;: &nbsp;
                            </span>
                            <span className="form-item">
                                {props.payment.phone}
                            </span>
                        </div>
                        <div className="form-item">
                            <span className="form-item__title">
                                Email
                            </span>
                            <span>
                                &nbsp;: &nbsp;
                            </span>
                            <span className="form-item">
                                {props.payment.email}
                            </span>
                        </div>
                    </div>
                </div>
                <div className="col-12 col-lg-4 mr-10">
                    <div className="method-form">
                        <p className="title">
                            Thng tin ngi nhn
                        </p>
                        <div className="form-item">
                            <span className="form-item__title">
                                S ti khon ngi nhn
                            </span>
                            <span>
                                &nbsp;: &nbsp;
                            </span>
                            <span className="form-item__content">
                                0651000853526
                            </span>
                        </div>
                        <div className="form-item">
                            <span className="form-item__title">
                                Ngn hng ngi nhn
                            </span>
                            <span>
                                &nbsp;: &nbsp;
                            </span>
                            <span className="form-item__content">
                                ACB
                            </span>
                        </div>
                        <div className="form-item">
                            <span className="form-item__title">
                                H v tn ngi nhn
                            </span>
                            <span>
                                &nbsp;: &nbsp;
                            </span>
                            <span className="form-item__content">
                                Nguyn Hoi Phong
                            </span>
                        </div>
                        <div className="form-item">
                            <span className="form-item__title">
                                Khuyn mi
                            </span>
                            <span>
                                &nbsp;: &nbsp;
                            </span>
                            <span className="form-item__content">
                                {props.payment.reward}%
                            </span>
                        </div>
                        <div className="form-item">
                            <span className="form-item__title">
                                Tng tin phi thnh ton
                            </span>
                            <span>
                                &nbsp;: &nbsp;
                            </span>
                            <span className="form-item__content" style={{ fontStyle: 'italic', fontWeight: 600, color: '#01bbbc' }}>
                                {(props.payment.price * (100 - props.payment.reward) / 100).toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}
                            </span>
                        </div>
                    </div>
                </div>
                <div className="btn-wrapper">
                    <a href onClick={() => props.setCurrent(2)} className="bk-btn">Xc Nhn</a>
                </div>
            </div>
        </div>
    );
}

export default Banking;