import { Form, Input } from 'antd';
import React from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import './styles.scss';

function FormRegister() {
    const location = useLocation();
    const [form] = Form.useForm();
    const history = useHistory()
    const toLogin = () => {
        history.push({
            pathname: '/dang-nhap',
            search: `?id=${location.search.slice(4)}`,
        })
    }
    const onFinish = (values) => {
        history.push({
            pathname: '/dang-nhap',
            search: `?id=${location.search.slice(4)}`,
        })
    };

    return (
        <Form form={form} onFinish={onFinish} className="form-signup">
            <div className="form-signup-text">
                <h3>ng k</h3>
                <p>ng t ti khon  s dng c nhiu chc nng trn website hn !</p>
            </div>
            <div className="form-signup-group">
                <label>Ti khon</label>
                <Form.Item name="username" rules={[{ required: true, message: 'Vui lng nhp ti khon !' }]}>
                    <Input type="text" className="form-signup-group-control" placeholder="Ti khon" />
                </Form.Item>
            </div>
            <div className="form-signup-group">
                <label>Mt khu</label>

                <Form.Item name="password" rules={[{ required: true, message: 'Vui lng nhp mt khu !' }]}>
                    <Input type="password" className="form-signup-group-control" placeholder="Mt khu" />
                </Form.Item>
            </div>
            <div className="form-signup-group">
                <label>Nhp li mt khu</label>
                <Form.Item name="rePassword" rules={[{ required: true, message: 'Vui lng nhp li mt khu !' }]}>
                    <Input type="password" className="form-signup-group-control" placeholder="Nhp li mt khu" />
                </Form.Item>
            </div>
            <div className="form-signup-group">
                <label>H v tn</label>
                <Form.Item name="fullname" rules={[{ required: true, message: 'Vui lng nhp h v tn !' }]}>
                    <Input type="text" className="form-signup-group-control" placeholder="Nhp h v tn" />
                </Form.Item>
            </div>
            <div className="form-signup-group">
                <label>Email</label>
                <Form.Item name="email" rules={[{ required: true, message: 'Vui lng nhp email !' }]}>
                    <Input type="text" className="form-signup-group-control" placeholder="Nhp email" />
                </Form.Item>
            </div>
            <div className="form-signup-group">
                <label>S in thoi</label>
                <Form.Item name="rePassword" rules={[{ required: true, message: 'Vui lng nhp s in thoi !' }]}>
                    <Input type="text" className="form-signup-group-control" placeholder="Nhp s in thoi" />
                </Form.Item>
            </div>
            <button type="submit" className="form-signup-btn-submit">ng k</button>
            <div className="auth-seperator">
                <span>
                    OR
                </span>
            </div>
            <p className="text-end">
                Bn  c ti khon?
                <a href onClick={() => toLogin()}> ng nhp</a>
            </p>
        </Form>
    );
}

export default FormRegister;